import React, { useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import {

    HomeContainer,
    ProfilePicture,
    LoadProfilePicture,
    NameTitle,
    TextLarge,
    TextMedium,
    ContactCont,
    FieldCont,
    Icon,
    ExternalLink,
    NavContainer

} from './homeComponents';

const contactInfo = [

    { 
        
        icon: "icon-linkedin",
        href: "https://www.linkedin.com/in/mirsad02/",
        text: "/in/mirsad02"
    
    },
    { 
        
        icon: "icon-github",
        href: "https://github.com/rosinant3",
        text: "/rosinant3"
    
    },
    {

        icon: "icon-envelope",
        href: "mailto:mirsad.avdic22@gmail.com",
        text: "mirsad.avdic22@gmail.com"

    }

];

const renderContact = () => {

    return contactInfo.map((info:any)=>{

        const icon = info.icon;
        const href = info.href;
        const text = info.text;

        return <FieldCont key={info.icon}>
        <Icon className={icon}></Icon>
        <TextMedium>
            <ExternalLink 
            
                href={href}
                target="_blank" 
                rel="noopener noreferrer"
                
            >{text}
            </ExternalLink>
        </TextMedium>
        </FieldCont>;

    });

}

interface HomeProps {



}

const Home: React.FC<HomeProps> = () => {

    const ref = useRef(null);
    const [ opacity, setOpacity ] = useState(0);
    document.title = "Mirsad Avdic | Bachelor of Science in Safety Engineering";

 
    return (<HomeContainer ref={ref}>
        <LoadProfilePicture 

            src="/profile.jpg" 
            onLoad={()=>{ setOpacity(1); }} 
            onError={()=>{ setOpacity(1); }}

        ></LoadProfilePicture>
        <ProfilePicture style={{ opacity: opacity, backgroundImage: "url('/profile.jpg')" }}></ProfilePicture>
        <NameTitle>
            <TextLarge>Mirsad Avdic</TextLarge>
            <TextMedium>Bachelor of Science in Safety Engineering</TextMedium>
        </NameTitle>   
        <ContactCont>
            {renderContact()}
        </ContactCont>
        <NavContainer>
            <Link to={`/?p=1`} >Web Projects</Link>
        </NavContainer>
    </HomeContainer>);


}

export default Home;

