import styled from 'styled-components';

export const HomeContainer = styled.div`

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    height: 100vh;


`;

export const ProfilePicture = styled.div`

    height: 325px;
    width: 325px;
    border-radius: 50%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(95, 162, 161, 1);
    transition-property: opacity;
    transition-duration: 500ms;

    @media (max-width: 520px) {

        height: 250px;
        width: 250px;

    }

    @media (max-width: 300px) {

        height: 250px;
        width: 95%;

    }

`;

export const LoadProfilePicture = styled.img`

    display: none;

`;

export const NameTitle = styled.div`

    display: flex;
    flex-direction: column;
    margin-top: 0.5rem;

`;

export const TextLarge = styled.div`

    padding: 0.25rem;
    font-size: 1.2rem;
    color: rgba(95, 162, 161, 1);
    font-weight: bold;

`;

export const TextMedium = styled.div`

    padding: 0.25rem;
    font-size: 0.9rem;
    color: rgba(95, 162, 161, 1);
    font-style: italic;

`;

export const ContactCont = styled.div`

    display: flex;
    flex-direction: row;
    margin-top: 1rem;
    aling-items: center;

    @media (max-width: 520px) {

        flex-direction: column;

    }

`;

export const FieldCont = styled.div`

    display: flex;
    flex-direction: row;
    margin-right: 1rem;

`;

export const ExternalLink = styled.a`

    color: rgba(95, 162, 161, 1);
    text-decoration: none;
    transition-property: opacity;
    transition-duration: 1s;
    opacity: 1;

    &:hover {

        opacity: 0.5;

    }

`;

export const Icon = styled.div`

    color: rgba(95, 162, 161, 1);
    font-size: 1.1rem;
    margin-right: 0.25rem;
    display: flex;
    align-items: center;

`;

export const NavContainer = styled.div`

    padding: 1rem;

    a {

        color: rgba(95, 162, 161, 1);
        text-decoration: none;
        font-weight: bold;
        transition-property: opacity;
        transition-duration: 1s;
        opacity: 1;
    
        &:hover {
    
            opacity: 0.5;
    
        }

    }

`;